.adminLoginPw {
    height: calc(100vh - 200px);
    display: flex;
    justify-content: center;
    align-items: center;
    table.MuiTable-root {
        width: 240px;
        height: 400px;
        .MuiTableBody-root {
            width: 240px;
            height: 400px;
            .MuiTableRow-root {
                height: 80px;
                width: 240px;
                .MuiTableCell-root {
                    padding: 0;
                    width: 80px;
                    height: 80px;
                    border: 0;
                    input {
                        width: 100%;
                        height: 100%;
                        font-size: 60px;
                        text-align: center;
                        border: 0;
                        outline: 0;
                        background-color: #111;
                        color: #fff;
                        font-family: var(--digital-font);
                    }
                    button {
                        width: 80px;
                        height: 80px;
                        background-color: #444;
                        border: 0;
                        color: #fff;
                        outline: 0;
                        cursor: pointer;
                        &.loginBtn {
                            width: 160px;
                            background-color: #333;
                        }
                        &:active {
                            background-color: black;
                        }
                    }
                }
            }
        }
    }
}
